import { Container, Drawer, List, ListItem, ListItemButton, Toolbar } from "@mui/material";
import { useEffect } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { store } from "../../../redux/store";
import { navStyle } from "../../../utils/style";

function Company(): JSX.Element {
    const navigate = useNavigate()

    useEffect(() => {
        if (store.getState().authState.userType !== "COMPANY") {
            navigate("/login")
        }
    })

    return (
        <div className="company text-center">
            <h3>Company Page</h3> <br />
            <Container fixed>
                <Drawer
                    variant="permanent"
                    anchor="right"
                    sx={{
                        width: "drawerWidth",
                        flexShrink: 0,
                        [`& .MuiDrawer-paper`]: { width: "drawerWidth", boxSizing: 'border-box' },
                    }}>
                    <Toolbar />
                    <br />
                    <List>
                        <ListItem disablePadding>
                            <ListItemButton>
                                <NavLink style={navStyle} to={"/company/getCompanyCoupons"}>All Coupons</NavLink>
                            </ListItemButton>
                        </ListItem>
                        <ListItem disablePadding>
                            <ListItemButton>
                                <NavLink style={navStyle} to={"/userDetails"}>Company Details</NavLink>
                            </ListItemButton>
                        </ListItem>
                    </List>
                </Drawer>
            </Container>
        </div>
    );
}

export default Company;
