import { Box, ThemeProvider, Typography } from "@mui/material";
import { useState } from "react";
import { NavLink, useNavigate } from 'react-router-dom';
import { store } from '../../redux/store';
import { userLogout } from '../../redux/authState';
import { title } from "../../utils/style";

var handleLog: any

export const setLoggedIn = () => {
    handleLog()
}

export default function Header(): JSX.Element {
    const navigate = useNavigate()
    const [client, setClient] = useState(store.getState().authState.userToken)

    handleLog = () => {
        setClient(store.getState().authState.userType)
    }

    const handleLogOut = () => {
        store.dispatch(userLogout())
        store.getState().couponState.coupons = []
        store.getState().companyState.companies = []
        store.getState().customerState.customers = []
        store.getState().cartState.couponsInCart = []
        setClient("")
        navigate("/")
    }

    const handleProfile = () => {
        switch (store.getState().authState.userType) {
            case "CUSTOMER":
                navigate("/customer")
                break;
            case "COMPANY":
                navigate("/company")
                break;
            case "ADMINISTRATOR":
                navigate("/admin")
                break;
        }
    }

    return (
        <div className="header text-light" style={{ zIndex: 1301 }}>
            <Box sx={{ bgcolor: "info.main" }}>
                <Box sx={{ ml: 3 }} >
                    <ThemeProvider theme={title}>
                    <Typography variant="h3">
                        Coupons
                    </Typography>
                    </ThemeProvider>
                </Box>
                <Box
                    sx={{
                        textAlign: "end",
                        paddingRight: 5,
                    }}>
                    <NavLink
                        style={{
                            color: "white",
                            textDecoration: "none",
                            paddingRight: 20,
                        }}
                        to={"/cart"}>
                        <Typography display={"inline"}> Cart</Typography>
                    </NavLink>

                    {client && <Typography onClick={() => handleLogOut()} variant="body1" style={{ color: "white", textTransform: 'none', textDecoration: "none", paddingRight: 20, display: 'inline', cursor: "pointer" }} >
                        Log Out
                    </Typography>}

                    {client && <Typography onClick={() => handleProfile()} variant="body1" style={{ color: "white", textTransform: 'none', textDecoration: "none", paddingRight: 3, display: 'inline', cursor: "pointer" }} >
                        Profile
                    </Typography>}

                    {!client && <NavLink
                        style={{
                            color: "white",
                            textDecoration: "none",
                            paddingRight: 3
                        }}
                        to={"/login"}>
                        <Typography display={"inline"}>Login</Typography>
                    </NavLink>}
                </Box>
            </Box>
        </div>
    );
}



