import { Button, Card, CardActions, CardContent, CardMedia, Typography } from "@mui/material";
import { Coupon } from "../../model/coupon";
import { addCouponToCart } from "../../redux/cartState";
import { store } from "../../redux/store";

interface couponCardProps {
    coupon: Coupon
}

function CouponCard(props: couponCardProps): JSX.Element {

    const addToCart = () => {
        store.dispatch(addCouponToCart(props.coupon))
    }

    return (
        <>
            <Card>
                <CardMedia component="img" height="140"
                    image={props.coupon.image} />
                <CardContent>
                    <Typography variant="h5" component="div">
                        {props.coupon.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondry">
                        {props.coupon.description} <br />
                        {props.coupon.price + "$"} <br />
                        {"Only " + props.coupon.amount + " left!"} <br />
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button onClick={() => addToCart()}>
                        ADD TO CART
                    </Button>
                </CardActions>
            </Card>
        </>
    );
}

export default CouponCard;
